import React, { useState, useEffect } from 'react';
import { Search } from 'lucide-react';
import { TextField, InputAdornment, List, ListItem, Typography, Box, Chip } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useTheme } from '@mui/material/styles';


const RightSideBar = () => {
    const [searchQuery, setSearchQuery] = useState('');
    const [searchResults, setSearchResults] = useState([]);
    const navigate = useNavigate();
    const theme = useTheme();

    const siteContent = [
        {
            path: '/skills',
            section: 'Comptences',
            items: [
                {
                    text: "Frontend Development",
                    subsection: "Technologies Web",
                    preview: "ReactJS (85%), Javascript (85%), Typescript (80%), HTML (90%), CSS (85%), Python (85%), Flask (80%)"
                },
                {
                    text: "Backend & Database",
                    subsection: "Technologies Serveur",
                    preview: "Spring Boot (75%), Java (75%), PostgreSQL (80%), MySQL (80%), SQL (85%)"
                },
                {
                    text: "DevOps & Tools",
                    subsection: "Outils de Dveloppement",
                    preview: "Docker (75%), Docker Compose (75%), Git (85%), GitLab (80%), Jira (75%)"
                },
                {
                    text: "Soft Skills",
                    subsection: "Comptences Transversales",
                    preview: "Proactif avec forte capacit d'anticipation, Force de propositions et solutions innovantes, Adaptabilit aux changements, Esprit d'quipe et communication positive"
                }
            ]
        },
        {
            path: '/experiences',
            section: 'Expriences',
            items: [
                {
                    text: "Dveloppement Full Stack - Gestion de projet",
                    subsection: "SOLUTEC Paris (Sep 2024 - Dc 2024)",
                    preview: "Dveloppement React.js, Architecture microservices Spring Boot, Base de donnes PostgreSQL, Conteneurisation Docker"
                },
                {
                    text: "Dveloppement Full Stack - Notes de frais",
                    subsection: "SOLUTEC Lyon (Avr 2023 - Sep 2023)",
                    preview: "Rdaction du dossier de cadrage, Dveloppement React.js et Spring Boot, Tests unitaires et d'intgration, Conteneurisation Docker"
                },
                {
                    text: "Dveloppement Systme - Segmentation audio-vido",
                    subsection: "LIFAT Tours (Juin 2022 - Sep 2022)",
                    preview: "Conception systme segmentation audio-vido, Structuration et indexation des donnes, Optimisation des algorithmes"
                }
            ]
        },
        {
            path: '/education',
            section: 'Education',
            items: [
                {
                    text: "Diplme d'ingnieur en Informatique",
                    subsection: "Polytech Tours (2020-2023)",
                    preview: "Systmes d'Informations, Architecture logicielle, Java, C#, C++, Python, Javascript, React, Vue, HTML/CSS, Base de donnes, Scurit"
                },
                {
                    text: "Classe prparatoire ATS",
                    subsection: "Lyce Blaise Pascal (2019-2020)",
                    preview: "Mathmatiques, Physique, Analyse, Rsolution de problmes complexes, Russite au concours d'entre en cole d'ingnieurs"
                },
                {
                    text: "BTS Topographe-gomtre",
                    subsection: "Lyce le Corbusier (2017-2019)",
                    preview: "Gomatique, Topographie, Cartographie, SIG, Formation certifie en logiciels de gomatique"
                }
            ]
        },
        {
            path: '/projects',
            section: 'Projets',
            items: [
                {
                    text: "HubTask - Application de gestion de projets",
                    subsection: "Gestion de Projets Collaboratifs",
                    preview: "Application web complte avec React, Spring Boot, Spring Security, PostgreSQL, WebSocket, Docker, Gestion de projets, tches et chat en temps rel"
                },
                {
                    text: "CineMatch - Recommandation de films",
                    subsection: "Application de suggestion personnalise",
                    preview: "Application React avec TmdbAPI, Tailwind CSS, Material-UI, systme de recommandation bas sur le profil utilisateur"
                },
                {
                    text: "E-commerce Platform",
                    subsection: "Frontend Design",
                    preview: "Interface e-commerce avec React, Tailwind CSS, Framer-motion, Animations et design moderne"
                },
                {
                    text: "Application QVT",
                    subsection: "Visualisation de Donnes",
                    preview: "Application VueJS, Firebase, Vega-Lite pour l'analyse et l'affichage dynamique des rsultats de sondage QVT"
                },
                {
                    text: "Application BMX",
                    subsection: "Gestion de Comptitions",
                    preview: "Application web Python, Flask, SQLite, SQL_Alchemy, MVC pour la gestion des courses et tournois de BMX"
                },
                {
                    text: "Portfolio",
                    subsection: "Site Web Personnel",
                    preview: "Site web avec React, Tailwind CSS, Material-UI, prsentation intuitive des comptences et ralisations"
                }
            ]
        },
        {
            path: '/profile',
            section: 'Profil',
            items: [
                {
                    text: "Centres d'intrt - Jeux d'checs",
                    subsection: "Loisirs",
                    preview: "Passionn de jeux d'checs en apprentissage constant"
                },
                {
                    text: "Centres d'intrt - Animaux",
                    subsection: "Loisirs",
                    preview: "Amateur d'animaux et propritaire d'un chien"
                },
                {
                    text: "Langues - Anglais",
                    subsection: "Comptences Linguistiques",
                    preview: "TOEIC valid, communication professionnelle en anglais"
                },
                {
                    text: "Voyages - Afrique du Sud",
                    subsection: "Expriences Internationales",
                    preview: "Voyage d'exploration et d'adaptation en Afrique du Sud"
                },
                {
                    text: "Voyages - Madagascar",
                    subsection: "Expriences Internationales",
                    preview: "Dcouverte culturelle et exploration  Madagascar"
                }
            ]
        }
    ];

    // Fonction pour extraire les mots-cls du contenu
    const extractKeywords = (item) => {
        const keywords = new Set();

        // Ajoute tous les mots du texte, sous-section et preview
        const addWords = (text) => {
            text.toLowerCase()
                .split(/[\s,/()]+/) // Spare sur les espaces, virgules, slash et parenthses
                .filter(word => word.length > 2) // Ignore les mots trop courts
                .forEach(word => keywords.add(word));
        };

        // Extrait les pourcentages et les nombres
        const extractNumbers = (text) => {
            const numbers = text.match(/\d+(?:\.\d+)?%?/g) || [];
            numbers.forEach(num => keywords.add(num));
        };

        // Extrait les technologies spcifiques
        const extractTechnologies = (text) => {
            const techRegex = /(React|JavaScript|TypeScript|HTML|CSS|Spring Boot|Java|Docker|Git|SQL|API|Vue|Firebase|PostgreSQL|MySQL)/gi;
            const technologies = text.match(techRegex) || [];
            technologies.forEach(tech => keywords.add(tech.toLowerCase()));
        };

        [item.text, item.subsection, item.preview].forEach(text => {
            if (text) {
                addWords(text);
                extractNumbers(text);
                extractTechnologies(text);
            }
        });

        return Array.from(keywords);
    };

    // Enrichir le contenu avec des mots-cls
    const enrichedContent = siteContent.map(section => ({
        ...section,
        items: section.items.map(item => ({
            ...item,
            keywords: extractKeywords(item)
        }))
    }));

    useEffect(() => {
        if (searchQuery.trim() === '') {
            setSearchResults([]);
            return;
        }

        const searchTerms = searchQuery.toLowerCase().split(/\s+/);

        const results = enrichedContent.flatMap(section =>
            section.items
                .filter(item => {
                    // Vrifie si tous les termes de recherche correspondent  au moins un mot-cl
                    return searchTerms.every(term =>
                        item.keywords.some(keyword => keyword.includes(term)) ||
                        item.text.toLowerCase().includes(term) ||
                        item.preview.toLowerCase().includes(term) ||
                        item.subsection.toLowerCase().includes(term)
                    );
                })
                .map(item => ({
                    ...item,
                    section: section.section,
                    path: section.path
                }))
        );

        setSearchResults(results);
    }, [searchQuery]);

    const handleResultClick = (path) => {
        navigate(path);
        setSearchQuery('');
    };

    return (
        <aside className="fixed right-0 w-64 h-screen bg-white shadow-lg overflow-y-auto"
            style={{ backgroundColor: theme.palette.background.paper }}>
            <div className="p-4">
                <TextField
                    type="search"
                    placeholder="Rechercher..."
                    variant="outlined"
                    fullWidth
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <Search className="h-4 w-4 text-gray-400" />
                            </InputAdornment>
                        ),
                    }}
                    sx={{
                        mb: 2,
                        '& .MuiOutlinedInput-root': {
                            backgroundColor: 'background.default',
                        },
                    }}
                />

                {searchResults.length > 0 && (
                    <List sx={{ mt: 2 }}>
                        {searchResults.map((result, index) => (
                            <ListItem
                                key={index}
                                button
                                onClick={() => handleResultClick(result.path)}
                                sx={{
                                    mb: 2,
                                    borderRadius: 1,
                                    border: '1px solid',
                                    borderColor: 'grey.200',
                                    '&:hover': {
                                        backgroundColor: 'grey.100',
                                    },
                                }}
                            >
                                <Box sx={{ width: '100%' }}>
                                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                                        <Typography variant="subtitle2" color="primary">
                                            {result.subsection}
                                        </Typography>
                                        <Chip
                                            label={result.section}
                                            size="small"
                                            sx={{
                                                backgroundColor: 'primary.light',
                                                color: 'primary.main',
                                            }}
                                        />
                                    </Box>
                                    <Typography variant="body2"
                                        sx={{
                                            mb: 1,
                                            display: '-webkit-box',
                                            WebkitLineClamp: 2,
                                            WebkitBoxOrient: 'vertical',
                                            overflow: 'hidden',
                                        }}
                                    >
                                        {result.text}
                                    </Typography>
                                    <Typography
                                        variant="caption"
                                        color="text.secondary"
                                        sx={{
                                            display: '-webkit-box',
                                            WebkitLineClamp: 1,
                                            WebkitBoxOrient: 'vertical',
                                            overflow: 'hidden',
                                        }}
                                    >
                                        {result.preview}
                                    </Typography>
                                </Box>
                            </ListItem>
                        ))}
                    </List>
                )}

                {searchQuery && searchResults.length === 0 && (
                    <Typography
                        variant="body2"
                        color="text.secondary"
                        sx={{ mt: 2, textAlign: 'center' }}
                    >
                        Aucun rsultat trouv
                    </Typography>
                )}
            </div>
        </aside>
    );
};

export default RightSideBar;